import React from "react";
import { NavLink } from "react-router-dom";

const Navbar = () => {
  return (
    <nav className="navbar">
      <div className="brand">
        <NavLink to="/">SaaD Khatri</NavLink>
      </div>
      <div
        className="hamburger"
        onClick={() => {
          const ham = document.querySelector(".hamburger");
          const ul = document.querySelector("nav ul");
          ham.classList.toggle("active");
          ul.classList.toggle("active");
        }}
      >
        <span></span>
        <span></span>
        <span></span>
      </div>
      <ul>
        <li>
          <NavLink
            exact
            to="/"
            activeClassName="activeAnchor"
            onClick={() => {
              const ul = document.querySelector("ul");
              ul.classList.remove("active");
            }}
          >
            Home
          </NavLink>
        </li>

        <li>
          <NavLink
            to="/services"
            activeClassName="activeAnchor"
            onClick={() => {
              const ul = document.querySelector("ul");
              ul.classList.remove("active");
            }}
          >
            Services
          </NavLink>
        </li>

        <li>
          <NavLink
            activeClassName="activeAnchor"
            to="/about"
            onClick={() => {
              const ul = document.querySelector("ul");
              ul.classList.remove("active");
            }}
          >
            About
          </NavLink>
        </li>

        <li>
          <NavLink
            activeClassName="activeAnchor"
            to="/contact"
            onClick={() => {
              const ul = document.querySelector("ul");
              ul.classList.remove("active");
            }}
          >
            Contact
          </NavLink>
        </li>
        <div className="register">
          <a>Sign Up</a>
          <a>Sign In</a>
        </div>
      </ul>
    </nav>
  );
};

export default Navbar;
